import Vue from 'vue';
import Router from 'vue-router';
import store from './store';
import jQuery from 'jquery'
import Home from './views/Home';
import Auth from './views/Auth';
import Signin from "./components/login/Signin";
import Signup from "./components/login/Signup";
import Recover from "./components/login/Recover";
import Menu from "./views/Menu";
import Profile from "./views/Profile";
import Carp from "./views/Carp"
import CarpPj from "./views/carppj/CarpPj"

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/auth',
      component: Auth,
      children: [
        {
          path: '',
          name: 'signin',
          component: Signin
        },
        {
          path: 'signup',
          name: 'signup',
          component: Signup
        },
        {
          path: 'recover',
          name: 'recover-password',
          component: Recover
        }
      ]
    },
    {
      path: '/menu',
      name: 'menu',
      component: Menu
    },
    {
      path: '/profile',
      name: 'profile',
      component: Profile
    },
    {
      path: '/changepassword',
      name: 'changepassword',
      component: () => import('./views/ChangePassword')
    },
    {
      name: 'recad-menu',
      path: '/recad',
      component: () => import('./views/recad/Menu')
    },
    {
      path: '/recad/form',
      component: () => import('./views/recad/Recadastramento'),
      children: [
        {
          path: '',
          name: 'recad-form-dadospessoais',
          component: () => import('./views/recad/forms/DadosPessoais')
        },
        {
          path: '2',
          name: 'recad-form-documentacao',
          component: () => import('./views/recad/forms/Documentacao')
        },
        {
          path: '3',
          name: 'recad-form-endereco',
          component: () => import('./views/recad/forms/Endereco')
        },
        {
          path: '4',
          name: 'recad-form-contato',
          component: () => import('./views/recad/forms/Contato')
        },
        {
          path: '5',
          name: 'recad-form-foto',
          component: () => import('./views/recad/forms/Foto')
        },
        {
          path: '6',
          name: 'recad-form-assinatura',
          component: () => import('./views/recad/forms/Assinatura')
        }
      ]
    },
    {
      name: 'recad-historico',
      path: '/recad/historico',
      component: () => import('./views/recad/Historico')
    },
    {
      name: 'recadpj-menu',
      path: '/recadpj',
      component: () => import('./views/recadpj/Menu')
    },
    {
      name: 'recadpj-empresas',
      path: '/recadpj/empresas',
      component: () => import('./views/recadpj/Empresas')
    },
    {
      name: 'recadpj-form',
      path: '/recadpj/form',
      component: () => import('./views/recadpj/RecadPj'),
      children: [
        {
          name: 'recadpj-1',
          path: '',
          component: () => import('./components/recadpj/PjDados')
        },
        {
          name: 'recadpj-2',
          path: '2',
          component: () => import('./components/recadpj/PjEndereco')
        },
        {
          name: 'recadpj-3',
          path: '3',
          component: () => import('./components/recadpj/PjContato')
        },
        {
          name: 'recadpj-4',
          path: '4',
          component: () => import('./components/recadpj/PjSocios')
        }
      ]
    },
    {
      name: 'palavra-cofeci',
      path: '/palavracofeci',
      component: () => import('./views/PalavraCofeci')
    },
    {
      name: 'palavra-creci',
      path: '/palavracreci',
      component: () => import('./views/PalavraCreci')
    },
    {
      name: 'carp',
      path: '/carp',
      component: Carp
    },
    {
      name: 'carp-pj-empresas',
      path: '/carppj',
      component: () => import('./views/carppj/Empresas')
    },
    {
      name: 'carp-pj',
      path: '/carppj/empresa/:cnpj',
      component: CarpPj
    },
    // {
    //   name: 'help',
    //   path: '/help',
    //   component: () => import('./views/help/Menu')
    // },
    {
      name: 'report',
      path: '/report',
      component: () => import('./views/Report')
    }
  ]
});

router.beforeEach((to, from, next) => {
  jQuery('.modal').modal('hide');
  if (store.state.token) {
    if (to.path.startsWith('/auth')) next('/');
    else next();
  }
  else {
    if (to.path.startsWith('/auth')) next();
    else next('/auth');
  }
});

export default router
