<template>
  <div class="home">
    <!-- Container maré azul -->
    <div class="bg-primary d-flex justify-content-center align-items-center flex-column"
         style="height: 18rem; position: relative; overflow: hidden">
      <!-- Foto de perfil -->
      <img :src="$store.getters.avatar" alt="" class="avatar">
      <!-- Situação -->
      <p class="text-light mb-0">Situação</p>
      <span v-if="$store.state.situacao === 0" class="badge badge-light text-danger">INATIVO</span>
      <span v-else-if="$store.state.situacao === 1" class="badge badge-light text-success">ATIVO</span>
      <span v-else-if="($store.state.situacao >= 2 && $store.state.situacao <= 4) || $store.state.situacao === 9 || $store.state.situacao === 11" class="badge badge-light text-danger">CANCELADO</span>
      <span v-else-if="$store.state.situacao === 5 || $store.state.situacao === 6 || $store.state.situacao === 10" class="badge badge-light text-danger">SUSPENSO</span>
      <!-- Animação de Onda -->
      <div style="height: 1.25rem">
        <div class="wave wave1"></div>
        <div class="wave wave2"></div>
      </div>
    </div>
    <!-- Informações -->
    <div class="container mt-3">
      <div v-for="alerta in alertas" :key="alerta.id" class="alert" role="alert"
           :class="{'alert-primary': alerta.tipo === 0, 'alert-secondary': alerta.tipo === 1, 'alert-success': alerta.tipo === 2, 'alert-danger': alerta.tipo === 3, 'alert-warning': alerta.tipo === 4, 'alert-info': alerta.tipo === 5, 'alert-light': alerta.tipo === 6, 'alert-dark': alerta.tipo === 7}">
        <h4 class="alert-heading" v-if="alerta.titulo"><i class="fas fa-exclamation-circle pr-2"></i>{{alerta.titulo}}</h4>
        <p class="mb-0">{{alerta.texto}}</p>
        <template v-if="alerta.rodape">
          <hr style="margin: .5rem 0">
          <p class="mb-0" style="font-weight: 600">{{alerta.rodape}}</p>
        </template>
      </div>
      <!-- Nome e Creci -->
      <div class="card mb-2">
        <div class="card-body p-2 d-flex align-items-center">
          <img src="../assets/icon/id_card.svg" alt="" style="width: 3rem; max-height: 3rem">
          <div class="flex-fill pl-2 ml-2 border-left text-center">
            <p class="mb-0">{{$store.state.nome}}</p>
            <h5 class="mb-0 text-uppercase">CRECI-{{$store.getters.uf + ' ' +$store.state.creci}}</h5>
          </div>
        </div>
      </div>
      <!-- Anuidade e Recadastramento -->
      <div class="row my-2">
        <div class="col-6 pr-1">
          <div class="card">
            <div class="card-body p-2 d-flex flex-column justify-content-center align-items-center">
              <p class="mb-0">Anuidade</p>
              <h5 class="mb-0 text-uppercase" v-if="$store.state.anuidade === true"><span class="badge badge-success">Em dia</span></h5>
              <h5 class="mb-0 text-uppercase" v-else-if="$store.state.anuidade === false"><span class="badge badge-danger">Pendente</span></h5>
              <h5 class="mb-0 text-uppercase" v-else><span class="badge badge-warning">???</span></h5>
            </div>
          </div>
        </div>
        <div class="col-6 pl-1">
          <div class="card">
            <div class="card-body p-2 d-flex flex-column justify-content-center align-items-center">
              <p class="mb-0">Recadastramento</p>
              <h5 class="mb-0 text-uppercase" v-if="$store.state.recadastrado"><span class="badge badge-success">Em dia</span></h5>
              <h5 class="mb-0 text-uppercase" v-else><span class="badge badge-danger">Pendente</span></h5>
            </div>
          </div>
        </div>
      </div>
      <!-- Npa -->
      <div class="card my-2" v-if="$store.state.npa">
        <div class="card-body p-2 d-flex align-items-center">
          <img src="../assets/icon/interview.svg" alt="" style="width: 3rem; max-height: 3rem">
          <div class="flex-fill pl-2 ml-2 border-left text-center">
            <p class="mb-0">NOME PROFISSIONAL ABREVIADO</p>
            <h5 class="mb-0 text-uppercase">{{$store.state.npa}}</h5>
          </div>
        </div>
      </div>
      <!-- Cnai -->
      <div class="card my-2" v-if="$store.state.cnai">
        <div class="card-body p-2 d-flex align-items-center">
          <img src="../assets/icon/house_search.svg" alt="" style="width: 3rem; max-height: 3rem">
          <div class="flex-fill pl-2 ml-2 border-left text-center">
            <p class="mb-0">AVALIADOR IMOBILIÁRIO</p>
            <h5 class="mb-0 text-uppercase">CNAI {{$store.state.cnai.toString().replace(/[^0-9]/g, '')}}</h5>
          </div>
        </div>
      </div>
      <!-- Empresas como responsavel tecnico -->
      <template v-if="$store.state.pj">
        <div class="card my-2" v-for="empresa in $store.state.pj" :key="empresa.id">
          <div class="card-body p-2 d-flex align-items-center">
            <img src="../assets/icon/company.svg" alt="" style="width: 3rem; max-height: 3rem">
            <div class="flex-fill pl-2 ml-2 border-left text-center">
              <span class="badge badge-info">Responsável Técnico</span>
              <p class="mb-0">{{empresa.nome}}</p>
              <h5 class="mb-0 text-uppercase">CRECI {{empresa.creci}} PJ</h5>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'home',
    data: () => ({
      alertas: [] //{id: Number, tipo: Number, texto: String, titulo: String, rodape: String}
    }),
    created() {
      //=== Procura alertas para exibicao ===
      // Busca no cache off-line.
      if (sessionStorage.getItem('alertas')) this.alertas = JSON.parse(sessionStorage.getItem('alertas'));
      // Tenta usar a internet para atualizar a lista de avisos e faz cacheamento para disponibilizar off-line.
      this.$http.axios.get(this.$http.appApi.defaults.baseURL + '/aviso/listar.php')
        .then(r => {this.alertas = r.data; sessionStorage.setItem('alertas', JSON.stringify(this.alertas))});
    }
  }
</script>

<style scoped>
  .wave {
    background-image: url('../assets/img/waves.png');
    background-size: 50% .75rem;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    height: 101%;
    width: 200%;
    background-position: 0 bottom;
    background-repeat: repeat-x;
    transform-origin: center bottom;
    animation: waves 6s linear infinite;
  }
  .wave2 {
    background-image: url('../assets/img/waves.png');
    background-size: 50% 1.25rem;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    height: 101%;
    width: 200%;
    background-position: 0 bottom;
    background-repeat: repeat-x;
    transform-origin: center bottom;
    animation: waves2 8s linear infinite;
    opacity: .35;
  }
  @keyframes waves {
    0% {
      transform: translateX(0) translateY(0) scaleY(1)
    }
    0% {
      transform: translateX(-25%) translateY(0) scaleY(.5)
    }
    0% {
      transform: translateX(-50%) translateY(0) scaleY(1)
    }
  }
  @keyframes waves2 {
    0% {
      transform: translateX(0) translateY(0) scaleY(1)
    }
    0% {
      transform: translateX(-25%) translateY(0) scaleY(.5)
    }
    0% {
      transform: translateX(-50%) translateY(0) scaleY(1)
    }
  }
  .avatar {
    height: 12rem;
    max-width: 9rem;
    object-fit: cover;
    background-color: white;
    border-radius: .5rem;
    box-shadow: .1rem .1rem .1rem rgba(0,0,0,.35);
    margin-bottom: .5rem;
  }
  .avatar + p + .badge {
    font-size: 1.2rem;
  }
</style>
