<template>
  <div id="app" :class="{'largura-restrita': desktop}">
    <!-- interface principal -->
    <Interface :exibir-controles="$store.state.controls">
      <!-- view container -->
      <template slot="conteudo">
        <transition name="translate" mode="out-in">
          <router-view/>
        </transition>
      </template>

      <!-- botoes de navegacao -->
      <template slot="tabnav">
        <nav-item to="/menu">
          <i class="fas fa-bars"></i>Menu
        </nav-item>
        <nav-item to="/">
          <i class="fas fa-home"></i>Home
        </nav-item>
        <nav-item to="/profile">
          <i class="fas fa-user"></i>Conta
        </nav-item>
      </template>
    </Interface>

    <!-- blackout spinner -->
    <transition name="fade">
      <div class="blackout" v-if="$store.state.loading">
        <div class="spinner-border" role="status">
          <span class="sr-only">Carregando...</span>
        </div>
        <p v-if="typeof $store.state.loading === 'string'" class="mb-0 mt-2">{{$store.state.loading}}</p>
      </div>
    </transition>

    <!-- Modal Web - Marketing para plataforma mobile -->
    <div class="modal fade" tabindex="-1" role="dialog" ref="modal-store">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Baixe nosso App</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <!-- Texto -->
            <p>O <strong>i-Corretor</strong> está disponível para smartphone. Baixe nosso App e tenha nossas facilidades no seu bolso.</p>
            <div class="stores my-sm-4">
              <!-- Android -->
              <a href='https://play.google.com/store/apps/details?id=br.net.conselho.app&pcampaignid=pcampaignidMKT-Other-global-all-co-prtnr-py-PartBadge-Mar2515-1'>
                <img class="img-fluid" alt='Disponível no Google Play' src='./assets/img/play_store.png'/>
              </a>
              <!-- iOS -->
              <a href="https://apps.apple.com/br/app/i-corretor/id1492642109">
                <img src="./assets/icon/ios_store.svg" alt="Disponível na App Store">
              </a>
            </div>
            <small class="text-muted">O <strong>i-Corretor</strong> concluiu sua migração para smartphones, sendo assim, em razão da acessibilidade e segurança a versão Web em breve será descontinuada.</small>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Interface from "./components/Interface";
  import NavItem from "./components/NavItem";
  export default {
    components: {NavItem, Interface},
    data: () => ({
      desktop: false
    }),
    mounted() {
      if (!this.$store.state.mobile) {
        // === PLATAFORMA WEB ===
        this.$jquery(this.$refs['modal-store']).modal('show');
      }
    }
    // created() {
    //   // Como o App foi desenhado para plataforma mobile, execuções em
    //   // desktop terão larguras limitadas para imitar a dimensão mobile
    //   const plataforma = window.navigator.platform;
    //   const plataformasDesktop = ['Win32', 'Win64', 'Windows', 'WinCE', 'Macintosh', 'MacIntel'];
    //   this.desktop = plataformasDesktop.indexOf(plataforma) !== -1;
    // }
  }
</script>

<style scoped>
  .blackout{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, .7);
    z-index: 1060;
    color: #ffffff
  }
  .spinner-border{
    width: 3rem;
    height: 3rem;
  }

  /* Modal de Marketing das Stores */
  .stores {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
  }
  .stores img {
    width: 10rem;
    margin: .6rem;
  }
</style>
