import Vue from 'vue';
import Vuex from 'vuex';
import api from "./plugins/http";
import TokenHelper from "./helpers/TokenHelper";
import AvatarMas from './assets/icon/avatar_homem.svg';
import AvatarFem from './assets/icon/avatar_mulher.svg';
import CreciHelper from "./helpers/CreciHelper";
import router from './router';
import ImageHelper from "./helpers/ImageHelper";
import toastr from "toastr";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    mobile: true,        //DEFINA SE A PLATAFORMA É "MOBILE" OU "WEB".
    homologacao: false,   //DEFINA SE O AMBIENTE É PRODUÇÃO OU HOMOLOGAÇÃO.
    controls: false,      //exibe o menu de navegação
    loading: false,       //tela de carregamento | true = exibe só o spinner | string = exibe uma mensagem |
    token: null,          //token de login
    nome: null,           //nome do usuário
    creci: null,          //numero de creci
    npa: null,            //nome profissional abreviado||false
    cnai: null,           //numero de cnai||false
    situacao: null,       //situacao perante o conselho //0 = Inativo (exclusivo API COFECI), 1 = Ativo, 2 = Cancelado a Pedido, 3 = Cancelado Punitivamente, 4 = Cancelado Por Débito, 5 = Suspenso, 6 = Suspenso Punitivamente, 7 = Trasferido, 8 = Falecido, 9 = Cancelado a Pedido R.T., 10 = Suspenso por Débito, 11 = Cancelado por Ato Administrativo
    foto: null,           //foto do corretor (base64)
    sexo: null,           //sexo do usuário // 1 = masc, 2 = femi, 3 = indefinido
    anuidade: null,       //true = em dia, false = em débito - representa se o corretor está APTO ou INAPTO ao exercício
    anuidade_absoluta: null, //true = em dia, false = em débito - representa se o corretor está integralmente quitado com o CRECI, dando direito ao cartão de regularidade.
    anuidade_exercicio_anterior: null, //true = em dia, false = em débito. O corretor pode estar apto a usar o cartão de regularidade do ano passado caso não ultrapassou 31 de março do ano atual
    naturalidade: null,   //cidade natal
    naturalidade_uf: null,//sigla do estado natal
    nacionalidade: null,
    pai: null,
    mae: null,
    datainscricao: null,  //em AAAA-MM-DD
    datanascimento: null, //em AAAA-MM-DD
    tipo: null,           //1 = Inscrição Principal, 2 = Secundária
    categoria: null,      //1 = Pessoa Física, 2 = Pessoa Jurídica, 3 = Estagiario
    formacao: null,       //1 = Corretor de Imóveis, 2 = TTI, 3 = Gestor
    doador: null,         //1 = Sim, 2 = Não
    estadocivil: null,    //1 = Solteiro, 2 = Casado, 3 = Viuvo, 4 = Separado, 5 = Separado judicialmente, 6 = Divorciado, 7 = desquitado, 8 = união estável
    rg: null,
    rgemissor: null,
    rgufemissor: null,
    rgdataemissao: null,
    tituloeleitoral: null,
    zonaeleitoral: null,
    secaoeleitoral: null,
    endereco_residencial: {
      cep: '',
      uf: '',
      cidade: '',
      bairro: '',
      logradouro: '',
      numero: '',
      complemento: ''
    },
    endereco_comercial: { //NULL -> não possui endereço comercial
      cep: '',
      uf: '',
      cidade: '',
      bairro: '',
      logradouro: '',
      numero: '',
      complemento: ''
    },
    endereco_favorito: null, //1-residencial, 2-comercial
    email1: null,     //primario
    email2: null,     //secundario
    email3: null,     //terciario
    telefones: [],    // {tipo: Number, numero: String} | 1-residencial,2-comercial,3-celular
    assinatura: null, //base64 | null
    reservista: null,
    recadastrado: null,//true = apto | false = pendente/inapto
    recadastramentos: null, //[] array com objetos que contem dados historicos do recad
    pj: null, //[] array das empresas que ele for responsavel tecnico, contendo os dados de cada uma (array de objetos).
    //Dados do presidente
    presidente_nome: null,
    presidente_foto: null, //base64
    presidente_assinatura: null, //base64
    presidente_texto: null,
    presidente_tipo: null, //0 = presidente, 1 = em exercicio, 2 = interventor
    //Dados do secretario
    secretario_nome: null,
    secretario_foto: null,
    secretario_assinatura: null,
    //Configuracoes do App
    recadConfig: null
    //tipo sanguineo e escolaridade faltam ser estudados para disponilizar no conselhonet
  },
  mutations: {
    setControls (state, payload) {
      state.controls = payload;
    },
    setLoading (state, payload) {
      state.loading = payload;
    },
    setToken (state, payload) {
      state.token = payload;
    },
    setDadosPessoais (state, payload) {
      state.nome = !payload ? null : payload.nome;
      state.creci = !payload ? null : payload.creci;
      state.npa = !payload ? null : payload.npa;
      state.cnai = !payload ? null : payload.cnai;
      state.situacao = !payload ? null : payload.situacao;
      state.sexo = !payload ? null : payload.sexo;
      state.anuidade = !payload ? null : payload.anuidade;
      state.anuidade_absoluta = !payload ? null : payload.anuidade_absoluta;
      state.anuidade_exercicio_anterior = !payload ? null : payload.anuidade_exercicio_anterior;
      state.naturalidade = !payload ? null : payload.naturalidade;
      state.naturalidade_uf = !payload ? null : payload.naturalidade_uf;
      state.nacionalidade = !payload ? null : payload.nacionalidade;
      state.pai = !payload ? null : payload.pai;
      state.mae = !payload ? null : payload.mae;
      state.datainscricao = !payload ? null : payload.datainscricao;
      state.datanascimento = !payload ? null : payload.datanascimento;
      state.tipo = !payload ? null : payload.tipo;
      state.categoria = !payload ? null : payload.categoria;
      state.formacao = !payload ? null : payload.formacao;
      state.doador = !payload ? null : payload.doador;
      state.estadocivil = !payload ? null : payload.estadocivil;
      state.rg = !payload ? null : payload.rg;
      state.rgemissor = !payload ? null : payload.rgemissor;
      state.rgufemissor = !payload ? null : payload.rgufemissor;
      state.rgdataemissao = !payload ? null : payload.rgdataemissao;
      state.tituloeleitoral = !payload ? null : payload.tituloeleitoral;
      state.zonaeleitoral = !payload ? null : payload.zonaeleitoral;
      state.secaoeleitoral = !payload ? null : payload.secaoeleitoral;
      state.endereco_residencial = !payload ? null : payload.endereco_residencial;
      state.endereco_comercial = !payload ? null : payload.endereco_comercial;
      state.endereco_favorito = !payload ? null : payload.endereco_favorito;
      state.email1 = !payload ? null : payload.email1;
      state.email2 = !payload ? null : payload.email2;
      state.email3 = !payload ? null : payload.email3;
      state.telefones = !payload ? [] : payload.telefones;
      state.reservista = !payload ? null : payload.reservista;
      state.recadastrado = !payload ? null : payload.recadastrado;
      state.recadastramentos = !payload ? null : payload.recadastramentos;
      state.pj = !payload ? null : payload.pj;
    },
    setFoto (state, payload) {
      state.foto = payload;
    },
    setAssinatura (state, payload) {
      state.assinatura = payload;
    },
    setPresidente (state, payload) {
      state.presidente_nome = payload.nome;
      state.presidente_foto = payload.foto;
      state.presidente_assinatura = payload.assinatura;
      state.presidente_texto = payload.texto;
      state.presidente_tipo = payload.tipo;
    },
    setSecretario (state, payload) {
      state.secretario_nome = payload.nome;
      state.secretario_foto = payload.foto;
      state.secretario_assinatura = payload.assinatura;
    },
    setRecadConfig (state, payload) {
      state.recadConfig = payload;
    },
    setMobile (state, payload) {
      state.mobile = payload; //Boolean
    }
  },
  actions: {
    initialiseStore({state, commit, dispatch}) {
      //Esta função é evocada sempre que o aplicativo é iniciado
      if (localStorage.getItem('store')) {
        let store = JSON.parse(localStorage.getItem('store')); //Obtem o "state" da store salva.
        store.mobile = state.mobile;
        store.homologacao = state.homologacao;
        store = Object.assign(state, store); //Substitui os dados do state atual pela state recuperada.
        //Define a exibição da barra de navegação caso o usuário esteja logado
        commit('setControls', !!state.token);
        //Valida se o login do usuário é autentico
        if (state.token) {
          this.replaceState(store);
          dispatch('validateToken', { somente_ultimo: true, emitir_erro: true })
            .then(() => {
              //Tenta atualizar os dados do usuário
              dispatch('updateUserData');
            })
            .catch(error => {
              if (error.request.status) dispatch('signout');
            });
        }
      }
    },
    signin({commit, dispatch}, token) {
      //loga o usuario atraves do token, retorna uma Promisse
      commit('setToken', token);
      return new Promise(async (resolve, reject) => {
        try {
          await dispatch('updateUserData');
          commit('setControls', true);
          resolve();
        } catch (e) {
          commit('setToken', null);
          reject(e);
        }
      });
    },
    signout({commit}) {
      commit('setDadosPessoais', null);
      commit('setFoto', null);
      commit('setToken', null);
      commit('setControls', false);
      localStorage.clear();
      return router.push({name: 'signin'});
    },
    async updateUserData({commit, getters, dispatch, state}) {
      //Preenche/atualiza os dados da Store. Retorna uma Promisse
      return new Promise(async (resolve, reject) => {
        //Separa a tratativa para membros do ConselhoNET
        if (CreciHelper.utilizaConselhoNet(getters.regional)) {
          try {
            let formData = new FormData();
            formData.append("cpf", getters.cpf);
            formData.append("idregional", getters.regional);
            const r = await api.axios.all([
              api.conselhoApi.post('/corretor/ficha.php', formData),
              api.conselhoApi.post('/corretor/obter/foto.php', formData, {responseType: 'blob', validateStatus: status => status===200 || status===404}),
              api.conselhoApi.post('/corretor/obter/assinatura.php', formData, {responseType: 'blob', validateStatus: status => status===200 || status===404})
            ]);

            //converte a formação do ConselhoNET para o App
            let formacao;
            const t = r[0].data.formacao.titulo;
            const c = r[0].data.info.categoria;
            if (t) {
              if (t===2) formacao = 1; //Corretor nivel médio
              if (t===5) formacao = 2; //Corretor nivel tecnico (TTI)
              if (t===1||t===3||t===4||t>=6) formacao = 3; //Corretor nivel superior Bacharel ou Tecnologo (Gestor Imobiliario)
            } else {
              if (c===1) formacao = 1;
              if (c===2) formacao = 2;
              if (c===3) formacao = 3;
            }

            //converte o estado civil do ConselhoNET para o App
            let estadocivil;
            switch (r[0].data.dados_gerais.estado_civil) {
              case 1:
                estadocivil = 1;
                break;
              case 2:
                estadocivil = 2;
                break;
              case 3:
                estadocivil = 3;
                break;
              case 4:
                estadocivil = 5;
                break;
              case 5:
                estadocivil = 6;
                break;
              case 6:
                estadocivil = 7;
                break;
              case 7:
                estadocivil = 8;
                break;
              default:
                estadocivil = 1;
            }

            //Adapta os dados do RG
            const rg = r[0].data.rg.identidade.trim();
            let rgemissor = r[0].data.rg.orgao_emissor.trim().replace(/ {2,}/g, ' '); //remove espaços iniciais, finais e duplicados
            let rgufemissor = '';
            if (rgemissor.match(/[-,/]/g)) {
              let ocorrencias = rgemissor.match(/[-,/]/g); //consulta a ocorrencia de caracteres divisores como / - ,
              let indiceUltimaOcorrencia = rgemissor.lastIndexOf(ocorrencias[ocorrencias.length - 1]); //pega o indice da ultima ocorrencia, que provavelmente separa o Emissor do UF
              //Descobrimento do UF
              rgufemissor = rgemissor.substring(indiceUltimaOcorrencia + 1).trim(); //Pega os digitos após o separador como UF
              if (rgufemissor.length < 2) rgufemissor = rgemissor.substr(rgemissor.length - 2); //Tratamento de erro. Previne UF menor que 2 carac. pegando apenas os dois ultimos da string do Emissor
              if (rgufemissor.length > 2) rgufemissor = rgufemissor.substr(rgemissor.length - 2); //Tratamento de erro. Previne UF maior que 2 carac. pegando apenas os dois ultimos
              //Remoção do UF da string do Emissor
              rgemissor = rgemissor.substring(0, indiceUltimaOcorrencia).trim();
            } else {
              rgufemissor = rgemissor.length >= 2 ? rgemissor.substr(rgemissor.length - 2) : '';
            }

            //aplica os dados na Store
            commit('setDadosPessoais', {
              nome: r[0].data.dados_gerais.nome,
              creci: r[0].data.dados_gerais.creci,
              npa: r[0].data.dados_gerais.npa ? r[0].data.dados_gerais.npa : false,
              cnai: r[0].data.cnai.cnai_numero ? r[0].data.cnai.cnai_numero : false,
              situacao: r[0].data.info.situacao,
              sexo: r[0].data.dados_gerais.sexo,
              anuidade: r[0].data.info.regular_financeiro,
              anuidade_absoluta: r[0].data.info.regular_financeiro_absoluto,
              anuidade_exercicio_anterior: r[0].data.info.regular_financeiro_exercicio_anterior,
              naturalidade: r[0].data.dados_gerais.naturalidade,
              naturalidade_uf: r[0].data.dados_gerais.uf_naturalidade,
              nacionalidade: r[0].data.dados_gerais.nacionalidade,
              pai: r[0].data.dados_gerais.pai,
              mae: r[0].data.dados_gerais.mae,
              datainscricao: r[0].data.dados_gerais.data_inscricao,
              datanascimento: r[0].data.dados_gerais.aniversario,
              tipo: r[0].data.dados_gerais.secundaria ? 2 : 1,
              categoria: (r[0].data.info.categoria !== 9) ? r[0].data.dados_gerais.tipo : 3,
              formacao: formacao,
              doador: r[0].data.info.doador ? 1 : 2,
              estadocivil: estadocivil,
              rg: rg,
              rgemissor: rgemissor,
              rgufemissor: rgufemissor,
              rgdataemissao: r[0].data.rg.data_emissao,
              tituloeleitoral: r[0].data.titulo_eleitor.titulo ? r[0].data.titulo_eleitor.titulo.trim().replace(/[^a-zA-Z0-9 ]/g, '') : '',
              zonaeleitoral: r[0].data.titulo_eleitor.zona ? r[0].data.titulo_eleitor.zona.trim().replace(/[^a-zA-Z0-9 ]/g, '') : '',
              secaoeleitoral: r[0].data.titulo_eleitor.secao ? r[0].data.titulo_eleitor.secao.trim().replace(/[^a-zA-Z0-9 ]/g, '') : '',
              endereco_residencial: {
                cep: r[0].data.contato.endereco_residencial.cep,
                uf: r[0].data.contato.endereco_residencial.estado,
                cidade: r[0].data.contato.endereco_residencial.cidade,
                bairro: r[0].data.contato.endereco_residencial.bairro,
                logradouro: r[0].data.contato.endereco_residencial.endereco,
                numero: '',
                complemento: ''
              },
              endereco_comercial: !r[0].data.contato.endereco_comercial ? null : {
                cep: r[0].data.contato.endereco_comercial.cep,
                uf: r[0].data.contato.endereco_comercial.estado,
                cidade: r[0].data.contato.endereco_comercial.cidade,
                bairro: r[0].data.contato.endereco_comercial.bairro,
                logradouro: r[0].data.contato.endereco_comercial.endereco,
                numero: '',
                complemento: ''
              },
              endereco_favorito: r[0].data.contato.endereco_correspondencia,
              email1: r[0].data.contato.emails[0],
              email2: r[0].data.contato.emails[1],
              email3: r[0].data.contato.emails[2],
              telefones : r[0].data.contato.telefones.residencial.map(tel => ({tipo: 1, numero: tel}))
                .concat(
                  r[0].data.contato.telefones.comercial.map(tel => ({tipo: 2, numero: tel})),
                  r[0].data.contato.telefones.celular.map(tel => ({tipo: 3, numero: tel}))
                ),
              reservista: r[0].data.dados_gerais.reservista ? r[0].data.dados_gerais.reservista.trim().replace(/[^a-zA-Z0-9 ]/g, '') : '',
              recadastrado: !!r[0].data.recadastramentos.find(recad => recad.exercicio === new Date().getFullYear()),
              recadastramentos: r[0].data.recadastramentos,
              pj: r[0].data.pj
            });
            if (r[1].status === 200) {
              let reader = new FileReader();
              reader.onloadend = () => commit('setFoto', reader.result);
              reader.readAsDataURL(r[1].data);
            }
            if (r[2].status === 200) {
              let reader = new FileReader();
              reader.onloadend = () => commit('setAssinatura', reader.result);
              reader.readAsDataURL(r[2].data);
            }
          } catch (e) {
            reject("Estamos sem comunicação com seu regional, tente mais tarde.");
          }
        }
        else {
          try {
            //obter os dados a partir da API COFECI
            let formData = new FormData();
            formData.append("cpf", getters.cpf);
            formData.append("token", CreciHelper.getCofeciToken(getters.regional));
            if (state.homologacao) formData.append('homologacao', 'S');
            const {data: resultados} = await api.axios.post(api.cofeciApi.defaults.baseURL + '/buscapessoafj.php', formData);
            const {data: resultadosValidados} = await api.axios.post(api.cofeciApi.defaults.baseURL + '/buscacorretor.php', formData); //Esta API só deve retornar os dados em caso de recadastramento em dia.

            //Tratamento de erros
            if (resultados.length === 1) {
              if (resultados[0].Erro !== null) throw resultados[0].Mensagem ? resultados[0].Mensagem : new Error();
            }

            //Procura o usuário nos resultados
            const dados = resultados.find(r => r.IdCreci === getters.regional);
            if (!dados) throw new Error("Não encontramos seu cadastro neste regional, tente mais tarde. Certifique-se que selecionou o regional correto e que sua ficha está atualizada.");
            const dadosValidados = resultadosValidados.find(r => r.IdCreci === getters.regional);

            //Aplica os dados na Store
            commit('setDadosPessoais', {
              nome: dados.Nome,
              creci: parseInt( dados.Creci.replace( /^\D+/g, '') ),
              npa: dados.NPA ? dados.NPA : false,
              cnai: dados.nCNAI ? dados.nCNAI : false,
              situacao: dados.Situacao === 'A' ? 1 : 0,
              sexo: dados.Profissao.toUpperCase().startsWith('CORRETORA') ? 2 : 1,
              anuidade: dados.DebitoCreci === 'N',
              anuidade_absoluta: dados.DebitoCreci === 'N', //não é adequado, mas não há outra forma
              anuidade_exercicio_anterior: dados.DebitoCreci === 'N', //não é adequado, mas não há outra forma
              naturalidade: dados.Naturalidade,
              naturalidade_uf: dados.UFNaturalidade,
              nacionalidade: 'BRASILEIRA',
              pai: dados.NomePai,
              mae: dados.NomeMae,
              datainscricao: dados.DataInscricao.substr(0, 10),
              datanascimento: dados.DataNascimento.substr(0, 10),
              tipo: 1, //inscricao principal. 2 = secundaria //pedir ao COFECI
              categoria: 1, //pessoa fisica
              formacao: dados.FormacaoEspecifica.toUpperCase().startsWith('GESTOR') ? 3 : (dados.FormacaoEspecifica.toUpperCase().startsWith('TÉCNICO') ? 2 : 1),
              doador: null,
              estadocivil: null,
              rg: dados.RegistroIdentidade,
              rgemissor: dados.OrgaoEmissor,
              rgufemissor: dados.UFOrgaoEmissor,
              rgdataemissao: '2000-01-01',
              tituloeleitoral: null,
              zonaeleitoral: null,
              secaoeleitoral: null,
              endereco_residencial: null,
              endereco_comercial: null,
              endereco_favorito: null,
              email1: null,
              email2: null,
              email3: null,
              telefones : null,
              reservista: null,
              recadastrado: !!dadosValidados, //tratar isso daqui URGENTE
              recadastramentos: [],
              pj: []
            });
            if (dados.Foto) commit('setFoto', "data:image/jpeg;base64," + dados.Foto);
            if (dados.Assinatura) commit('setAssinatura', "data:image/jpeg;base64," + dados.Assinatura);

            //Tratativa diferenciada para o CRECI-RJ e CRECI-MG. Atualiza a validade documento pela SPW.
            let dataValidadePadrao = ((new Date()).getFullYear() + 1) + '-04-15'; //para casos onde pagaram a vista, a SPW nao informa a data.
            if (getters.regional === 1) {
              let { data: financeiroRj } = await api.axios.get("https://servico.creci-rj.gov.br/spw/api_cadastral/CRECI/SituacaoCadastral_financeira", {
                headers: {
                  chave: '14facc52-dd80-4e60-b8e5-dbd1636e20ee',
                  cpf_cnpj: getters.cpf,
                  cadastroCompleto: 'NAO'
                }
              })
              financeiroRj = financeiroRj[0];

              //Atualiza o Cofeci
              api.cofeciApi.post('/validadedocumento.php', JSON.stringify({
                "Token": CreciHelper.getCofeciToken(getters.regional),
                "CodigoInscricao": state.creci,
                "IdCategoriaInscricao": 1,
                "Pessoa": "F",
                "IdSituacao": financeiroRj.dataValidade ? 2 : 1,
                "DataValidade": financeiroRj.dataValidade ? financeiroRj.dataValidade.substr(6,4) + "-" + financeiroRj.dataValidade.substr(3,2) + "-" + financeiroRj.dataValidade.substr(0,2) : dataValidadePadrao,
                "DebitoCreci": financeiroRj.situacaoFinanceira === "EM DIA" ? (financeiroRj.dataValidade ? "S" : "N") : "S",
                "homologacao": state.homologacao ? true : undefined
              }))
            }
            else if (getters.regional === 4) {
              let { data: financeiroMg } = await api.axios.get("https://crecimg2.spiderware.com.br/spw/api_cadastral/CRECI/SituacaoCadastral_financeira", {
                headers: {
                  chave: '1fc975e0-424b-4601-9edc-25c6e8b7d81b',
                  cpf_cnpj: getters.cpf,
                  cadastroCompleto: 'NAO'
                }
              })
              financeiroMg = financeiroMg[0];

              //Atualiza o Cofeci
              api.cofeciApi.post('/validadedocumento.php', JSON.stringify({
                "Token": CreciHelper.getCofeciToken(getters.regional),
                "CodigoInscricao": state.creci,
                "IdCategoriaInscricao": 1,
                "Pessoa": "F",
                "IdSituacao": financeiroMg.dataValidade ? 2 : 1,
                "DataValidade": financeiroMg.dataValidade ? financeiroMg.dataValidade.substr(6,4) + "-" + financeiroMg.dataValidade.substr(3,2) + "-" + financeiroMg.dataValidade.substr(0,2) : dataValidadePadrao,
                "DebitoCreci": financeiroMg.situacaoFinanceira === "EM DIA" ? (financeiroMg.dataValidade ? "S" : "N") : "S",
                "homologacao": state.homologacao ? true : undefined
              }))
            }
          } catch (e) {
            if (typeof e === "string") reject("MENSAGEM DO COFECI: " + e);
            else if (e.message) reject(e.message);
            else reject("Não foi possível obter seus dados com o COFECI, tente mais tarde.");
          }
        }

        try {
          dispatch('updatePresidenteData');
          dispatch('updateSecretarioData');
          dispatch('updateAppConfig');
        } finally {
          resolve();
        }
      })
    },
    async updatePresidenteData({commit, getters}) {
      //Pega da WEB os dados do presidente e armazena na store
      //O Promisse desta acion dispara Reject caso a conexão falhe, porém, nenhuma mensagem de erro sairá na tela do usuário
      const formData = new FormData();
      formData.append('idregional', getters.regional);
      const respostas = await api.axios.all([
        api.axios.post(api.appApi.defaults.baseURL + '/presidente/obter_presidente.php', formData),
        api.axios.get(api.appApi.defaults.baseURL + '/presidente/obter_presidente_foto.php?id=' + getters.regional, { responseType: 'blob', validateStatus: status => status===200 || status===404 }),
        api.axios.get(api.appApi.defaults.baseURL + '/presidente/obter_presidente_assinatura.php?id=' + getters.regional, { responseType: 'blob', validateStatus: status => status===200 || status===404 })
      ]);

      commit('setPresidente', {
        nome: respostas[0].data.nome,
        foto: respostas[1].status === 200 ? await ImageHelper.blobToDataURI_async(respostas[1].data) : null,
        assinatura: respostas[2].status === 200 ? await ImageHelper.blobToDataURI_async(respostas[2].data) : null,
        texto: respostas[0].data.texto,
        tipo: respostas[0].data.tipo
      });
    },
    async updateSecretarioData({commit, getters}) {
      //Pega da WEB os dados do secretario e armazena na store
      //O Promisse desta acion dispara Reject caso a conexão falhe, porém, nenhuma mensagem de erro sairá na tela do usuário
      const formData = new FormData();
      formData.append('idregional', getters.regional);
      const respostas = await api.axios.all([
        api.axios.post(api.appApi.defaults.baseURL + '/secretario/obter_secretario.php', formData),
        api.axios.get(api.appApi.defaults.baseURL + '/secretario/obter_secretario_foto.php?id=' + getters.regional, { responseType: 'blob', validateStatus: status => status===200 || status===404 }),
        api.axios.get(api.appApi.defaults.baseURL + '/secretario/obter_secretario_assinatura.php?id=' + getters.regional, { responseType: 'blob', validateStatus: status => status===200 || status===404 })
      ]);

      commit('setSecretario', {
        nome: respostas[0].data.nome,
        foto: respostas[1].status === 200 ? await ImageHelper.blobToDataURI_async(respostas[1].data) : null,
        assinatura: respostas[2].status === 200 ? await ImageHelper.blobToDataURI_async(respostas[2].data) : null,
      });
    },
    async updateAppConfig({commit, getters}) {
      //Pega da WEB as configurações do aplicativo conforme estão definidas no painel administrativo do i-Corretor
      //O Promisse desta acion dispara Reject caso a conexão falhe, porém, nenhuma mensagem de erro sairá na tela do usuário
      const {data: lista} = await api.appApi.get('/settings_recad/listar.php');
      const recadConfig = lista.find(i => i.regional === getters.regional);
      if (!recadConfig) throw new Error('Regional sem configuração');
      commit('setRecadConfig', recadConfig);
    },
    async validateToken({state}, payload) {
      //Valida o token de login, a função envia uma requisição para o servidor, e retorna a resposta.
      //Se a validação falhar, a reposta terá um código diferente de 200
      //payload = { somente_ultimo: Boolean, emitir_erro: Boolean }
      let r;
      const formData = new FormData();
      if (payload.somente_ultimo) formData.append('somente_ultimo', 'true');
      const url = api.appApi.defaults.baseURL + '/validaLogin.php';
      try {
        r = await api.axios.post(url, formData, {headers: {'Authorization': state.token}});
        return r;
      } catch (error) {
        if (payload.emitir_erro) if (error.response) if (error.response.data) if (error.response.data.mensagem) toastr.error(error.response.data.mensagem);
        return Promise.reject(error);
      }
    }
  },
  getters: {
    id: state => !state.token ? null : new TokenHelper(state.token).getDado('id'),
    cpf: state => !state.token ? null : new TokenHelper(state.token).getDado('cpf'),
    regional: state => !state.token ? null : new TokenHelper(state.token).getDado('regional'),
    desde: state => !state.token ? null : new TokenHelper(state.token).getDado('desde'),
    data_senha: state => !state.token ? null : new TokenHelper(state.token).getDado('data_senha'),
    avatar: state => state.foto ? state.foto : (state.sexo === 2 ? AvatarFem : AvatarMas),
    uf: (state, getters) => CreciHelper.idParaUf(getters.regional, true)
  }
})
