<template>
  <div class="card" style="width: 23rem; max-width: 95%">
    <div class="card-body">
      <header class="text-center text-success">
        <i class="fas fa-user-tie fa-3x"/>
        <h5 class="mt-2">Nova conta</h5>
      </header>
      <form @submit.prevent="submit" ref="form" autocomplete="off">
        <!-- regional -->
        <div class="form-group">
          <label for="ipt-regional">Regional</label>
          <select id="ipt-regional" class="form-control" required v-model="iptRegional">
            <option :value="null" disabled="disabled">SELECIONE</option>
            <option v-for="regional in regionais" :key="regional.id" :value="regional.id">
              {{regional.id}}º - {{regional.ufExtenso.toUpperCase()}}
            </option>
          </select>
          <small v-if="compatibilidadeParcial" class="form-text"><i class="fas fa-exclamation-triangle pr-2 text-warning"/>Regional sem convênio</small>
        </div>
        <!-- cpf -->
        <div class="form-group">
          <label for="ipt-cpf">CPF</label>
          <the-mask id="ipt-cpf" type="tel" class="form-control" mask="###.###.###-##" required v-model="iptCpf"/>
        </div>
        <!-- email -->
        <div class="form-group">
          <label for="ipt-email">E-mail</label>
          <input id="ipt-email" type="email" class="form-control" autocapitalize="off" required v-model.trim="iptEmail">
        </div>
        <!-- senha -->
        <div class="form-group">
          <label for="ipt-senha">Senha</label>
          <input id="ipt-senha" type="password" class="form-control"
                 minlength="6"
                 maxlength="25"
                 autocapitalize="off"
                 required
                 :class="{
                 'is-valid': iptSenha.length>=6 && iptSenha.indexOf(' ') === -1,
                 'is-invalid': (iptSenha.length>0 && iptSenha.length<6) || iptSenha.indexOf(' ') !== -1
                 }"
                 v-model="iptSenha">
          <span class="invalid-feedback" v-show="iptSenha.length>0 && iptSenha.length<6">Tamanho mínimo de 6 caracteres</span>
          <span class="invalid-feedback" v-show="iptSenha.indexOf(' ') !== -1">Não use espaço em branco</span>
          <small class="text-muted" v-show="!iptSenha">min. 6 dígitos</small>
        </div>
        <!-- repetição da senha -->
        <div class="form-group">
          <label for="ipt-senha2">Repita a senha</label>
          <input id="ipt-senha2" type="password" class="form-control"
                 autocapitalize="off"
                 required
                 :class="{
                 'is-valid': (iptSenha===iptSenha2) && iptSenha,
                 'is-invalid': (iptSenha!==iptSenha2) && iptSenha
                 }"
                 v-model="iptSenha2">
        </div>
        <!-- botoes -->
        <button ref="btnsubmit" type="submit" class="btn btn-success btn-block" :disabled="!senhasAprovadas || disableSubmit">
          CADASTRAR
        </button> <!-- apagar o :disabled quando fornecer suporte a todos os regionais -->
        <router-link :to="{name: 'signin'}" class="btn btn-secondary btn-block">
          VOLTAR
        </router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import BrazilHelper from "../../helpers/BrazilHelper";
  import CreciHelper from "../../helpers/CreciHelper";
  import {regionais} from "../../helpers/CreciHelper";
  export default {
    name: "Signup",
    data: () => ({
      regionais,
      iptRegional: null,
      iptCpf: '',
      iptSenha: '',
      iptSenha2: '',
      iptEmail: '',
      disableSubmit: false
    }),
    methods: {
      async submit() {
        if (!BrazilHelper.validarCpf(this.iptCpf)) {
          this.$toastr.warning("CPF inválido");
          return;
        }
        if (!this.senhasAprovadas) {
          this.$toastr.warning("Senha inválida");
          return;
        }
        this.formDisabled();
        let formData = new FormData();
        formData.append("cpf", this.iptCpf);
        formData.append("idregional", this.iptRegional);

        //Separa a tratativa para clientes do ConselhoNET
        if (CreciHelper.utilizaConselhoNet(this.iptRegional)) {
          //Utiliza ConselhoNET
          try {
            const res = await this.$http.conselhoApi.post('/corretor/ficha.php', formData);

            //é corretor pessoa física?
            if (res.data.dados_gerais.tipo !== 1) {
              this.$toastr.warning("Só é permitido pessoa física");
              throw new Error();
            }

            //estagiários ficam de fora, o CRECI deles na ficha do ConselhoNET é 0
            if (!res.data.dados_gerais.creci) {
              this.$toastr.warning("Só é permitido corretor de imóveis");
              throw new Error();
            }

            //o email confere com o do regional?
            if (!res.data.contato.emails.includes(this.iptEmail)) {
              this.$toastr.warning("Este e-mail não consta no cadastro do regional", "E-mail não autorizado");
              throw new Error();
            }

            //ja particiou da "Solenidade de Juramento & Entrega do Cartão" ?
            if (!res.data.info.solenidade_entrega) {
              this.$toastr.warning("Você não participou da 'Solenidade de Juramento e Entrega da Credencial'");
              throw new Error();
            }

            //criação de conta autorizada
            formData.append("email", this.iptEmail);
            formData.append("senha", this.iptSenha);
            const res2 = await this.$http.appApi.post('/usuarios/cadastrar.php', formData);
            this.$toastr.success(res2.data.msg);
            this.$router.push({name: 'signin', params: {iptRegional: this.iptRegional, iptCpf: this.iptCpf}});
          } catch (e) {
            this.formDisabled(false);
            if (e) if (e.response) if (e.response.config.baseURL !== this.$http.appApi.defaults.baseURL) if (e.response.data && e.response.headers) if (e.response.headers['content-type'].startsWith('application/json')) if (e.response.data.mensagem) {
              this.$toastr.error(e.response.data.mensagem);
            }
          }
        } else {
          //Não utiliza ConselhoNET
          formData.append('token', CreciHelper.getCofeciToken(this.iptRegional));
          if (this.$store.state.homologacao) formData.append('homologacao', 'S');
          try {

            //Procura o e-mail na base de dados do COFECI
            const {data: resultados} = await this.$http.axios.post(this.$http.cofeciApi.defaults.baseURL + '/buscaemailcorretor.php', formData);

            //Tratamento de erros
            if (resultados.length === 1 && resultados[0].Erro !== null) {
              let erro = parseInt(resultados[0].Erro);
              if (erro === 2) {
                this.$toastr.error('O CPF não consta na base de dados nacional de corretores de imóveis. Certifique-se com o seu regional se seu CRECI está ativo e gravado na base de dados do COFECI.');
              } else {
                this.$toastr.warning('Temporariamente indisponível');
              }
              throw new Error();
            }

            //Verifica se o e-mail está no resultado
            const filtrado = resultados.find(r => r.IdCreci === this.iptRegional && r.EmailPagina === this.iptEmail);
            if (!filtrado) {
              this.$toastr.warning("Este e-mail não consta na ficha nacional do corretor. Use outro e-mail ou peça ao seu regional que registre um novo e-mail no COFECI.", "E-mail não autorizado pelo COFECI");
              throw new Error();
            }

            //criação de conta autorizada
            formData.append("email", this.iptEmail);
            formData.append("senha", this.iptSenha);
            const res2 = await this.$http.appApi.post('/usuarios/cadastrar.php', formData);
            this.$toastr.success(res2.data.msg);
            this.$router.push({name: 'signin', params: {iptRegional: this.iptRegional, iptCpf: this.iptCpf}});
          } catch (e) {
            this.formDisabled(false);
          }
        }
      },
      formDisabled(desativar = true) {
        let form = this.$jquery(this.$refs.form);
        form.find('input, select').prop('disabled', desativar);
        this.disableSubmit = desativar;

        let btnsubmit = this.$jquery(this.$refs.btnsubmit);
        if (desativar) btnsubmit.html('<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>');
        else btnsubmit.html('REGISTRAR');
      }
    },
    created() {
      if (this.$route.query.cpf) this.iptCpf = this.$route.query.cpf;
      if (this.$route.query.regional) this.iptRegional = parseInt(this.$route.query.regional);
    },
    beforeRouteLeave (to, from, next) {
      if (to.path.startsWith('/auth/')) {
        if (this.iptRegional) to.query.regional = this.iptRegional;
        if (this.iptCpf) to.query.cpf = this.iptCpf;
      }
      next();
    },
    computed: {
      compatibilidadeParcial() {
        //o regional não usa sistema ConselhoNET, portanto, só depende da base de dados do COFECI
        return this.iptRegional !== null ? !CreciHelper.utilizaConselhoNet(this.iptRegional) : false;
      },
      senhasAprovadas() {
        return this.iptSenha.length >= 6 && this.iptSenha.indexOf(' ') === -1 && this.iptSenha === this.iptSenha2;
      }
    }
  }
</script>

<style scoped>
  .card-body {
    box-shadow: inset 0 4px 0 0 #17c671;
    border-radius: .625rem;
  }
</style>
