<template>
  <div class="carp-view">
    <!-- Loading Screen -->
    <div v-if="loading || !empresa">
      <div class="spinner-border text-primary" role="status">
        <span class="sr-only">Carregando...</span>
      </div>
      <p v-if="typeof loading === 'string'" class="mb-0 mt-2">{{loading}}</p>
    </div>
    <!-- CARP -->
    <div v-else>
      <transition name="flip" mode="out-in">
        <!-- Frente -->
        <div class="cartao frente" :key="1" v-if="frente">
          <!-- Fundo -->
          <img src="@/assets/img/cire_frente.png" alt="" class="img-fluid w-100">
          <!-- Dados -->
          <div class="dados text-center">
            <!-- Nome da empresa -->
            <p class="mb-1 px-2" style="font-weight: 500; font-size: 3.5vw; line-height: 1">{{empresa.nome}}</p>
            <!-- Creci da empresa -->
            <p class="mb-0" style="font-weight: 600; font-size: 4vw">CRECI-{{$store.getters.uf}} {{empresa.creci}}-J</p>
          </div>
          <!-- Validade -->
          <div class="validade">
            <div style="transform: rotateZ(-90deg); margin-bottom: 13vw; line-height: 1" class="d-flex flex-column justify-content-center align-items-start">
              <p class="mb-0" style="font-size: 1.6vw">VALIDADE</p>
              <p class="mb-0" style="font-size: 3.9vw">{{validade.dia}}/{{validade.mes}}/{{validade.ano}}</p>
            </div>
            <p class="mb-0" style="font-size: 8vw; transform: rotateZ(-90deg)">{{validade.ano}}</p>
          </div>
          <!-- Assinaturas dos presidentes -->
          <div class="assinaturas text-center">
            <!-- CRECI -->
            <div>
              <img :src="$store.state.presidente_assinatura" alt="">
              <div>
                <p class="mb-0 text-uppercase">{{$store.state.presidente_nome}}</p>
                <p class="mb-0" v-if="$store.state.presidente_tipo === 1">Presidente em exercício do CRECI{{$store.getters.uf ? '-' + $store.getters.uf : ''}}</p>
                <p class="mb-0" v-else-if="$store.state.presidente_tipo === 2">Presidente interventor do CRECI{{$store.getters.uf ? '-' + $store.getters.uf : ''}}</p>
                <p class="mb-0" v-else>Presidente do CRECI{{$store.getters.uf ? '-' + $store.getters.uf : ''}}</p>
              </div>
            </div>
            <!-- COFECI -->
            <div>
              <img :src="$http.appApi.defaults.baseURL + '/presidente/obter_presidente_assinatura.php?id=0'" alt="">
              <div>
                <p class="mb-0 text-uppercase">{{presidenteCofeci}}</p>
                <p class="mb-0">Presidente do COFECI</p>
              </div>
            </div>
          </div>
        </div>
        <!-- Verso -->
        <div class="cartao verso" :key="2" v-else>
          <!-- Fundo -->
          <img src="@/assets/img/cire_verso.png" alt="" class="img-fluid w-100">
          <!-- Nome da instituição -->
          <p class="titulo">Conselho Regional de Corretores de Imóveis<br/>CRECI {{$store.getters.regional}}ª Região - {{$store.getters.uf}}</p>
          <!-- QR Code -->
          <div class="qr-code">
            <qrcode
              :value="empresa.cnpj"
              :options="{ width: 256, margin: 1, errorCorrectionLevel: 'M' }"
            ></qrcode>
          </div>
        </div>
      </transition>
      <!-- Botões -->
      <div class="btn-group w-100 mt-3">
        <button class="btn btn-primary" @click="frente = !frente">VIRAR</button>
        <router-link class="btn btn-secondary" :to="{name: 'home'}">SAIR</router-link>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "CarpPj",
    data: () => ({
      loading: true,
      empresa: null,
      presidenteCofeci: '', // Nome do presidente.
      validade: {           // Validade do documento.
        ano: '2020',
        mes: '06',
        dia: '18'
      },
      frente: true          // Alterna entre frente/verso.
    }),
    methods: {
      loadEmpresa() {
        const empresa = this.$store.state.pj.find(e => e.cnpj === this.$route.params.cnpj);
        if (!empresa) {
          this.$toastr.error('Erro na identificação da empresa');
          this.$router.push('/');
          return;
        }
        this.empresa = empresa;
      },
      async loadPresidenteCofeci() {
        const formData = new FormData();
        formData.append('idregional', '0');
        const { data: presidente } = await this.$http.axios.post(this.$http.appApi.defaults.baseURL + '/presidente/obter_presidente.php', formData);
        this.presidenteCofeci = presidente.nome;
      },
    },
    async created() {
      if (!this.$route.params.cnpj) {
        this.$toastr.error('Erro na identificação da empresa');
        this.$router.push('/');
        return;
      }
      try {
        this.loadEmpresa();
        await this.loadPresidenteCofeci();
      } finally {
        this.loading = false;
      }
    },
    mounted() {
      this.$store.commit('setControls', false);
    },
    destroyed() {
      this.$store.commit('setControls', true);
    }
  }
</script>

<style scoped>
  .carp-view {
    display: flex;
    flex: 1 1 auto; /* preenche toda a altura a disposição */
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .cartao {
    position: relative;
    border-radius: .35rem;
    overflow: hidden;
    width: 94vw;
    box-shadow: .1rem .1rem .3rem rgba(0,0,0,.3);
    color: rgb(55,55,55);
  }
  .cartao.frente .dados {
    position: absolute;
    width: 91%;
    left: 0;
    bottom: 32%;
    font-family: Arial, Roboto, sans-serif;
  }
  .cartao.frente .assinaturas {
    position: absolute;
    width: 91%;
    left: 0;
    bottom: 8%;
    font-size: 2vw;
    line-height: 1;
    display: flex;
    justify-content: space-around;
  }
  .cartao.frente .validade {
    position: absolute;
    top: 0;
    right: .8%;
    width: 8.5%;
    height: 100%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .cartao.frente .assinaturas img {
    height: 10vw;
    max-width: 60%;
    object-fit: contain;
  }
  .cartao.verso  .titulo {
    position: absolute;
    top: 3%;
    left: 18%;
    width: 80%;
    margin: 0;
    text-align: center;
    font-size: 3.4vw;
    font-weight: 600;
  }
  .cartao.verso  .qr-code {
    position: absolute;
    width: 100%;
    height: 80%;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .cartao.verso  .qr-code * {
    width: 35vw !important;
    height: 35vw !important;
  }

  @media (orientation: landscape) {
    .carp-view {
      transform: scale(.75) translateY(-15%);
    }
    .btn-group .btn {
      padding: .75rem 1.75rem;
      font-size: 1.125rem;
      line-height: 1.5;
    }
  }
</style>