import Vue from 'vue'
import axios from 'axios'
import toastr from 'toastr'

const appApi = axios.create({
  timeout: 60000,
  baseURL: 'https://app.conselho.net.br/api/services' // https://app.conselho.net.br/api/services
});
appApi.interceptors.response.use(res => res, error => {
  if (error.response) {
    if (error.response.data) if (error.response.data.mensagem) toastr.error(error.response.data.mensagem);
  }
  else if (error.request) {
    if (error.request.status === 0) toastr.error('Sem conexão');
    else toastr.error('Erro ' + error.request.status);
  }
  // eslint-disable-next-line no-console
  else console.log('HTTP Error', error.message);
  return Promise.reject(error);
});

const conselhoApi = axios.create({
  timeout: 60000,
  baseURL: 'https://api.conselho.net.br'
});

const cofeciApi = axios.create({
  timeout: 60000,
  baseURL: 'https://app.conselho.net.br/api/bridge/cofeci' // https://apihomologa.cofeci.gov.br/api | https://api.cofeci.gov.br/api
});
cofeciApi.interceptors.response.use(res => {
  if (!res.data.TrueFalse) return Promise.reject(res);
  return res;
}, error => Promise.reject(error));

const http = {
  appApi,
  conselhoApi,
  cofeciApi,
  axios
};

Vue.prototype.$http = http;
export default http;