<template>
  <div class="card" style="width: 23rem; max-width: 95%">
    <div class="card-body">
      <header class="text-center text-warning">
        <i class="fas fa-key fa-3x"></i>
        <h5 class="mt-2">Recuperar acesso</h5>
      </header>
      <form @submit.prevent="submit" ref="form">
        <!-- regional -->
        <div class="form-group">
          <label for="ipt-regional">Regional</label>
          <select id="ipt-regional" class="form-control" required v-model="iptRegional">
            <option :value="null" disabled="disabled">SELECIONE</option>
            <option v-for="regional in regionais" :key="regional.id" :value="regional.id">
              {{regional.id}}º - {{regional.ufExtenso.toUpperCase()}}
            </option>
          </select>
        </div>
        <!-- cpf -->
        <div class="form-group">
          <label for="ipt-cpf">CPF</label>
          <the-mask id="ipt-cpf" type="tel" class="form-control" mask="###.###.###-##" required v-model="iptCpf"></the-mask>
        </div>
        <!-- email -->
        <div class="form-group">
          <label for="ipt-email">E-mail</label>
          <input id="ipt-email" type="email" class="form-control" required v-model="iptEmail">
        </div>
        <!-- botao -->
        <button ref="btnsubmit" type="submit" class="btn btn-warning btn-block" :disabled="disableSubmit">RECUPERAR</button>
        <router-link :to="{name: 'signin'}" class="btn btn-secondary btn-block">VOLTAR</router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import BrazilHelper from "../../helpers/BrazilHelper";
  import {regionais} from "../../helpers/CreciHelper";
  export default {
    name: "Recover",
    data: () => ({
      regionais,
      iptRegional: null,
      iptCpf: '',
      iptEmail: '',
      disableSubmit: false
    }),
    methods: {
      async submit() {
        this.formDisabled();
        try {
          if (!BrazilHelper.validarCpf(this.iptCpf)) {
            this.$toastr.warning("CPF inválido");
            throw new Error();
          }
          let formData = new FormData();
          formData.append("cpf", this.iptCpf);
          const res = await this.$http.appApi.post('/usuarios/buscar.php', formData);
          const info = res.data.find(c => c.regional === this.iptRegional);
          if (!info) {
            this.$toastr.error('Nenhuma conta corresponde as informações inseridas');
            throw new Error();
          }
          formData.append("idregional", this.iptRegional);
          const res2 = await this.$http.conselhoApi.post('/corretor/ficha.php', formData);
          if (!res2.data.contato.emails.includes(this.iptEmail)) {
            this.$toastr.warning("Este e-mail não consta no cadastro da conta", "E-mail inválido");
            throw new Error();
          }
          formData.append("email", this.iptEmail);
          await this.$http.appApi.post('/usuarios/reset.php', formData);
          this.$toastr.success('Enviamos uma nova senha para o e-mail', 'Senha alterada!');
          this.$router.push({name: 'signin'});
        } finally {
          this.formDisabled(false);
        }
      },
      formDisabled (desativar = true) {
        let form = this.$jquery(this.$refs.form);
        form.find('input, select').prop('disabled', desativar);
        this.disableSubmit = desativar;

        let btnsubmit = this.$jquery(this.$refs.btnsubmit);
        if (desativar) btnsubmit.html('<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>');
        else btnsubmit.html('RECUPERAR');
      }
    },
    created() {
      if (this.$route.query.cpf) this.iptCpf = this.$route.query.cpf;
      if (this.$route.query.regional) this.iptRegional = parseInt(this.$route.query.regional);
    },
    beforeRouteLeave (to, from, next) {
      if (to.path.startsWith('/auth/')) {
        if (this.iptRegional) to.query.regional = this.iptRegional;
        if (this.iptCpf) to.query.cpf = this.iptCpf;
      }
      next();
    }
  }
</script>

<style scoped>
  .card-body {
    box-shadow: inset 0 4px 0 0 #ffb400;
    border-radius: .625rem;
  }
</style>