<template>
  <ul class="list-group">
    <li class="list-group-item active text-center h5 mb-0">CONTA</li>
    <li class="list-group-item list-group-item-action" @click="$router.push({ name: 'changepassword' })">
      <img src="../assets/icon/key.svg" alt=""> Alterar senha
    </li>
    <li class="list-group-item list-group-item-action" @click="$router.push({ name: 'report' })">
      <img src="../assets/icon/email_alert.svg" alt=""> Reportar problema no App
    </li>
    <li class="list-group-item list-group-item-action" @click="logout">
      <img src="../assets/icon/door_out.svg" alt=""> Sair
    </li>
    <!-- Detalhes -->
    <li class="mt-3 mb-1 ml-2">Detalhes da Conta</li>
    <li class="list-group-item text-center p-2" style="font-weight: 400">
      <p class="mb-0">Usuário desde</p>
      <span class="badge badge-success">{{corrigeData($store.getters.desde).substr(0, 10)}}</span>
    </li>
    <li class="list-group-item text-center p-2" style="font-weight: 400">
      <p class="mb-0">Última alteração de senha</p>
      <span class="badge badge-success">{{this.$store.getters.data_senha ? corrigeData(this.$store.getters.data_senha) : 'NUNCA'}}</span>
    </li>
    <li class="text-center mt-3" style="list-style: none">versão {{version}}</li>
  </ul>
</template>

<script>
  import DateHelper from "../helpers/DateHelper";
  import JsonPackage from "../../package.json";
  export default {
    name: "Profile",
    data: () => ({
      version: JsonPackage.version
    }),
    methods: {
      logout() {
        this.$store.dispatch('signout');
      },
      corrigeData(datetime) {
        return DateHelper.datetime_SQLparaBR(datetime);
      }
    }
  }
</script>

<style scoped>
  .list-group-item img{
    height: 1.75rem;
    max-width: 1.75rem;
    object-fit: cover;
    margin-right: 1rem;
  }
  .list-group-item{
    border-radius: 0 !important;
  }
</style>
