<template>
  <div class="carp-view">
    <!-- Loading Screen -->
    <div v-if="loading">
      <div class="spinner-border text-primary" role="status">
        <span class="sr-only">Carregando...</span>
      </div>
      <p v-if="typeof loading === 'string'" class="mb-0 mt-2">{{loading}}</p>
    </div>
    <!-- Feedback de erros -->
    <div v-else-if="erro" class="d-flex flex-column align-items-center justify-content-center flex-fill">
      <article v-if="typeof erro === 'string'" class="text-center">
        <i class="fas fa-exclamation-circle fa-4x text-danger"/>
        <p style="font-weight: 600" class="px-1 mb-0 mt-3">{{erro}}</p>
      </article>
      <article v-else-if="erro === 1" class="text-center">
        <i class="fas fa-exclamation-circle fa-4x text-danger"/>
        <p style="font-weight: 600" class="px-1 mb-0 mt-3">Você não está regular</p>
      </article>
      <article v-else-if="erro === 2" class="text-center">
        <i class="fas fa-exclamation-circle fa-4x text-danger"/>
        <p style="font-weight: 600" class="px-1 mb-0 mt-3">Não foi possível obter as credenciais da diretoria do seu regional.<br/>Verifique sua conexão ou tente mais tarde.</p>
      </article>
      <article v-else-if="erro === 3" class="text-center">
        <i class="fas fa-exclamation-triangle fa-4x text-warning"/>
        <p style="font-weight: 600" class="px-1 my-3">Você está irregular com o recadastramento</p>
        <router-link v-if="podeRecadastrar" class="btn btn-primary" :to="{name: 'recad-form-dadospessoais'}">Realizar recadastramento</router-link>
      </article>
      <article v-else-if="erro === 4" class="text-center">
        <i class="fas fa-exclamation-circle fa-4x text-danger"/>
        <p style="font-weight: 600" class="px-1 mb-0 mt-3">Você não quitou integralmente os débitos do exercício com o regional</p>
      </article>
      <article v-else-if="erro === 5" class="text-center">
        <i class="fas fa-exclamation-circle fa-4x text-danger"/>
        <p style="font-weight: 600" class="px-1 mb-0 mt-3">Você não está ativo no conselho</p>
      </article>
    </div>
    <!-- CARP -->
    <div v-else>
      <transition name="flip" mode="out-in">
        <!-- Frente -->
        <div v-if="!visualizacao" class="cartao frente" key="frente">
          <!-- Fundo -->
          <img src="../assets/img/carp_frente.png" alt="" class="img-fluid w-100">
          <!-- Foto do corretor -->
          <img :src="$store.getters.avatar" alt="" class="avatar">
          <!-- Dados do corretor -->
          <div class="dados">
            <!-- Nome -->
            <p class="mb-0" style="font-size: 3.8vw; line-height: 1">{{$store.state.nome}}</p>
            <!-- Profissão com sexo -->
            <p class="mb-1" style="font-size: 3.2vw">{{$store.state.sexo === 2 ? 'Corretora de Imóveis' : 'Corretor de Imóveis'}}</p>
            <!-- Número de CRECI e regional -->
            <p class="mb-0" style="font-size: 4.4vw; font-weight: 600">CRECI-{{$store.getters.uf}} {{$store.state.creci}}</p>
            <!-- CNAI -->
            <p v-if="$store.state.cnai" class="mb-0" style="font-size: 3.5vw; font-weight: 700; color: red">CNAI {{$store.state.cnai}}</p>
          </div>
          <!-- Assinatura do corretor -->
          <div class="assinatura">
            <img :src="$store.state.assinatura" alt="">
          </div>
          <!-- Data de validade -->
          <div class="vencimento">
            <!-- Dia e mês -->
            <p class="diaVencimento">{{converteDoisDigitos(validadeDia) + '/' + converteDoisDigitos(validadeMes)}}</p>
            <!-- Ano do vencimento -->
            <p class="anoVencimento">{{validadeAno}}</p>
          </div>
        </div>
        <!-- Verso -->
        <div v-else class="cartao verso" key="verso">
          <!-- Fundo -->
          <img src="../assets/img/carp_verso.png" alt="" class="img-fluid w-100">
          <!-- QR -->
          <a :href="qrcode" target="_blank">
            <qrcode class="qr-code" :value="qrcode" :options="{ width: 256, margin: 1, errorCorrectionLevel: 'M' }"></qrcode>
          </a>
          <!-- Dados do corretor -->
          <div class="dados">
            <!-- Regional -->
            <p class="regional">{{regional}}</p>
            <!-- Nome do Pai -->
            <p class="pai">{{$store.state.pai}}</p>
            <!-- Nome da Mãe -->
            <p class="mae">{{$store.state.mae}}</p>
            <!-- Naturalidade -->
            <p class="naturalidade">{{$store.state.naturalidade}}-{{$store.state.naturalidade_uf}}</p>
            <!-- RG -->
            <p class="rg">{{$store.state.rg}} {{$store.state.rgemissor}}/{{$store.state.rgufemissor}}</p>
            <!-- Data de Nascimento -->
            <p class="datanascimento">{{datanascimento}}</p>
            <!-- CPF -->
            <p class="cpf">{{$store.getters.cpf.substr(0,3) + '.' + $store.getters.cpf.substr(3,3) + '.' + $store.getters.cpf.substr(6,3) + '-' + $store.getters.cpf.substr(9)}}</p>
            <!-- Data de Inscrição -->
            <p class="datainscricao">{{datainscricao}}</p>
            <!-- Formação -->
            <p class="formacao">{{formacao}}</p>
          </div>
          <!-- Presidente -->
          <section class="presidente">
            <img :src="$store.state.presidente_assinatura" alt="">
            <div class="presidente-dados">
              <p class="mb-0 text-uppercase">{{$store.state.presidente_nome}}</p>
              <p class="mb-0" v-if="$store.state.presidente_tipo === 1">Presidente em exercício do CRECI{{$store.getters.uf ? '-' + $store.getters.uf : ''}}</p>
              <p class="mb-0" v-else-if="$store.state.presidente_tipo === 2">Presidente interventor do CRECI{{$store.getters.uf ? '-' + $store.getters.uf : ''}}</p>
              <p class="mb-0" v-else>Presidente do CRECI{{$store.getters.uf ? '-' + $store.getters.uf : ''}}</p>
            </div>
          </section>
          <!-- Diretor Secretario -->
          <section class="secretario">
            <img :src="$store.state.secretario_assinatura" alt="">
            <div class="secretario-dados">
              <p class="mb-0 text-uppercase">{{$store.state.secretario_nome}}</p>
              <p class="mb-0">Diretor Secretário do CRECI{{$store.getters.uf ? '-' + $store.getters.uf : ''}}</p>
            </div>
          </section>
        </div>
      </transition>
      <div class="btn-group w-100 mt-3">
        <button class="btn btn-primary" @click="virarCartao">VIRAR</button>
        <router-link class="btn btn-secondary" :to="{name: 'home'}">SAIR</router-link>
      </div>
    </div>
  </div>
</template>

<script>
  import DateHelper from "../helpers/DateHelper";
  import CreciHelper from "../helpers/CreciHelper";
  export default {
    name: "Carp",
    data: () => ({
      erro: 0, //0 = sem erro, 1 = não está regular (genérico), 2 = erro de carregamento de dados, 3 = irregular com recadastramento, 4 = em débito,
      visualizacao: 0, // 0 = frente, 1 = verso
      //Data de validade: Por padrão 15/04/yyyy
      validadeDia: 15,
      validadeMes: 4,
      validadeAno: (new Date()).getFullYear() + 1,
      loading: true
    }),
    methods: {
      // Verifica se o usuário esta apto, atribuindo o valor adequado a variavel "erro"
      async validarAptidao() {
        this.loading = true;

        //==== Validações de aptidão ====//

        //Está ativo?
        if (CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) {
          if (this.$store.state.situacao !== 1) {
            this.erro = 5;
          }
        }

        //Fez o recadastramento?
        if (!this.erro && !this.$store.state.recadastrado) this.erro = 3;

        //Quitou os débitos?
        if (!this.erro && CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) {
          if (!this.$store.state.anuidade_absoluta) {
            //não quitou o exercício atual, então veremos se ainda pode usar o cartão do ano passado
            if (DateHelper.date_SQLagora() <= (DateHelper.date_SQLagora().substr(0,5) + '04-15') && this.$store.state.anuidade_exercicio_anterior) this.validadeAno--;
            else this.erro = 4;
          }
        }

        //Tratativa para CRECI-RJ e CRECI-MG. Importante saber que esse codigo já é usado na Action "updateUserData".
        if (!this.erro && !CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) {
          if (this.$store.getters.regional === 1) {
            let { data: financeiroRj } = await this.$http.axios.get("https://servico.creci-rj.gov.br/spw/api_cadastral/CRECI/SituacaoCadastral_financeira", {
              headers: {
                chave: '14facc52-dd80-4e60-b8e5-dbd1636e20ee',
                cpf_cnpj: this.$store.getters.cpf,
                cadastroCompleto: 'NAO'
              }
            })
            financeiroRj = financeiroRj[0];
            if (financeiroRj.situacaoCadastral !== "ATIVO") this.erro = 1;
            else if (financeiroRj.situacaoFinanceira !== "EM DIA") this.erro = 4;
            else if (financeiroRj.dataValidade) {
              //dataVencimentoProximaParcela é uma string em formato de "dd/mm/aaaa", senão é uma string vazia.
              this.validadeDia = parseInt(financeiroRj.dataValidade.substr(0,2));
              this.validadeMes = parseInt(financeiroRj.dataValidade.substr(3,2));
              this.validadeAno = parseInt(financeiroRj.dataValidade.substr(6,4));
            }
            //Atualiza o Cofeci
            this.$http.cofeciApi.post('/validadedocumento.php', JSON.stringify({
              "Token": CreciHelper.getCofeciToken(this.$store.getters.regional),
              "CodigoInscricao": this.$store.state.creci,
              "IdCategoriaInscricao": 1,
              "Pessoa": "F",
              "IdSituacao": financeiroRj.dataValidade ? 2 : 1,
              "DataValidade": financeiroRj.dataValidade ? financeiroRj.dataValidade.substr(6,4) + "-" + financeiroRj.dataValidade.substr(3,2) + "-" + financeiroRj.dataValidade.substr(0,2) : this.validadeAno + '-' + ((this.validadeMes < 10) ? '0' + this.validadeMes : this.validadeMes) + '-' + ((this.validadeDia < 10) ? '0' + this.validadeDia : this.validadeDia),
              "DebitoCreci": financeiroRj.situacaoFinanceira === "EM DIA" ? (financeiroRj.dataValidade ? "S" : "N") : "S",
              "homologacao": this.$store.state.homologacao ? true : undefined
            }))
          }
          else if (this.$store.getters.regional === 4) {
            let { data: financeiroMg } = await this.$http.axios.get("https://crecimg2.spiderware.com.br/spw/api_cadastral/CRECI/SituacaoCadastral_financeira", {
              headers: {
                chave: '1fc975e0-424b-4601-9edc-25c6e8b7d81b',
                cpf_cnpj: this.$store.getters.cpf,
                cadastroCompleto: 'NAO'
              }
            })
            financeiroMg = financeiroMg[0];
            if (financeiroMg.situacaoCadastral !== "ATIVO") this.erro = 1;
            else if (financeiroMg.situacaoFinanceira !== "EM DIA") this.erro = 4;
            else if (financeiroMg.dataValidade) {
              //dataVencimentoProximaParcela é uma string em formato de "dd/mm/aaaa", senão é uma string vazia.
              this.validadeDia = parseInt(financeiroMg.dataValidade.substr(0,2));
              this.validadeMes = parseInt(financeiroMg.dataValidade.substr(3,2));
              this.validadeAno = parseInt(financeiroMg.dataValidade.substr(6,4));
            }
            //Atualiza o Cofeci
            this.$http.cofeciApi.post('/validadedocumento.php', JSON.stringify({
              "Token": CreciHelper.getCofeciToken(this.$store.getters.regional),
              "CodigoInscricao": this.$store.state.creci,
              "IdCategoriaInscricao": 1,
              "Pessoa": "F",
              "IdSituacao": financeiroMg.dataValidade ? 2 : 1,
              "DataValidade": financeiroMg.dataValidade ? financeiroMg.dataValidade.substr(6,4) + "-" + financeiroMg.dataValidade.substr(3,2) + "-" + financeiroMg.dataValidade.substr(0,2) : this.validadeAno + '-' + ((this.validadeMes < 10) ? '0' + this.validadeMes : this.validadeMes) + '-' + ((this.validadeDia < 10) ? '0' + this.validadeDia : this.validadeDia),
              "DebitoCreci": financeiroMg.situacaoFinanceira === "EM DIA" ? (financeiroMg.dataValidade ? "S" : "N") : "S",
              "homologacao": this.$store.state.homologacao ? true : undefined
            }))
          }
        }

        //Tem foto e assinatura?
        if (!this.erro) {
          if (!this.$store.state.foto && !this.$store.state.assinatura) {
            this.erro = "Você não possui foto e assinatura, realize o recadastramento para inserir novas imagens ou compareça ao CRECI.";
          } else if (!this.$store.state.foto) {
            this.erro = "Você não possui foto, realize o recadastramento para inserir uma nova ou compareça ao CRECI."
          } else if (!this.$store.state.assinatura) {
            this.erro = "Você não possui assinatura, realize o recadastramento para inserir uma nova ou compareça ao CRECI"
          }
        }

        //==== Obtem credenciais dos diretores ====//
        if (!this.erro) {
          try {
            await this.$store.dispatch('updatePresidenteData');
            await this.$store.dispatch('updateSecretarioData');
          } finally {
            if (!this.$store.state.presidente_nome || !this.$store.state.presidente_assinatura) this.erro = 2;
            this.$store.commit('setControls', !!this.erro);
            this.loading = false;
          }
        } else {
          this.loading = false;
        }
      },
      // Vira a identidade para frente/verso
      virarCartao() {
        if (this.visualizacao === 0) this.visualizacao++;
        else this.visualizacao--;
      },
      converteDoisDigitos(digito = 0) {
        return digito < 10 ? '0'+digito : digito.toString;
      }
    },
    created() {
      this.validarAptidao();
    },
    destroyed() {
      this.$store.commit('setControls', true);
    },
    computed: {
      regional() {return "CRECI "+this.$store.getters.regional+"ª Região - " + CreciHelper.idParaUf(this.$store.getters.regional, true);},
      qrcode() {
        if (CreciHelper.utilizaConselhoNet(this.$store.getters.regional)) return "https://www.creci"+CreciHelper.idParaUf(this.$store.getters.regional, false)+".conselho.net.br/form_emite_certidao_regularidade.php?CRECI="+this.$store.state.creci;
        else return "http://creci"+CreciHelper.idParaUf(this.$store.getters.regional, false)+".gov.br";
      },
      datanascimento() {return DateHelper.date_SQLparaBR(this.$store.state.datanascimento)},
      datainscricao() {return DateHelper.date_SQLparaBR(this.$store.state.datainscricao)},
      formacao() {
        switch (this.$store.state.formacao) {
          case 2:
            return 'TÉCNICO EM TRANSAÇÕES IMOBILIÁRIAS';
          case 3:
            return 'GESTOR IMOBILIÁRIO';
          default:
            return 'CORRETOR DE IMÓVEIS (Lei nº 6.530/1978)';
        }
      },

      podeRecadastrar() {
        return CreciHelper.utilizaConselhoNet(this.$store.getters.regional);
      }
    }
  }
</script>

<style scoped>
  .carp-view {
    display: flex;
    flex: 1 1 auto; /* preenche toda a altura a disposição */
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .cartao {
    position: relative;
    border-radius: .35rem;
    overflow: hidden;
    width: 94vw;
    box-shadow: .1rem .1rem .3rem rgba(0,0,0,.3);
  }

  .cartao.frente img.avatar {
    position: absolute;
    top: 24.8%;
    right: 5.4%;
    height: 52.3%;
    width: 25%;
    object-fit: cover;
  }
  .cartao.frente .dados {
    position: absolute;
    width: 65%;
    top: 43%;
    left: 4%;
    line-height: 5vw;
  }
  .cartao.frente .dados p {
    text-align: center;
    color: rgb(55,55,55);
  }
  .cartao.frente .assinatura {
    position: absolute;
    bottom: 10%;
    left: 32%;
    width: 30%;
    height: 12%;
    display: flex;
    align-items: end;
  }
  .cartao.frente .assinatura img {
    max-width: 100%;
    max-height: 100%;
    display: block;
    object-fit: contain;
  }
  .cartao.frente .vencimento {
    position: absolute;
    bottom: 6%;
    right: 3.2%;
    display: flex;
    align-items: baseline;
    line-height: 1;
  }
  .cartao.frente .vencimento .diaVencimento {
    font-size: 5vw;
    color: red;
    margin-bottom: .35vw;
  }
  .cartao.frente .vencimento .anoVencimento {
    font-size: 7.8vw;
    font-weight: bold;
    color: red;
    margin-bottom: 0;
  }

  .cartao.verso .qr-code {
    position: absolute;
    top: 18.7%;
    left: 1.6%;
    width: 21vw !important;
    height: 21vw !important;
  }
  .cartao.verso .dados p {
    margin-bottom: 0;
    font-size: 2.5vw;
    font-weight: 600;
    color: rgb(55,55,55);
    position: absolute;
  }
  .cartao.verso .dados .regional {
    text-align: center;
    font-size: 2.9vw;
    font-weight: 700;
    width: 67%;
    top: 9%;
    left: 24%;
  }
  .cartao.verso .dados .pai {
    top: 18%;
    left: 34%;
  }
  .cartao.verso .dados .mae {
    top: 23%;
    left: 34%;
  }
  .cartao.verso .dados .naturalidade {
    top: 34%;
    left: 25%;
  }
  .cartao.verso .dados .rg {
    top: 43%;
    left: 25%;
  }
  .cartao.verso .dados .datanascimento {
    top: 34%;
    left: 70%;
  }
  .cartao.verso .dados .cpf {
    top: 43%;
    left: 70%;
  }
  .cartao.verso .dados .datainscricao {
    top: 52%;
    left: 70%;
  }
  .cartao.verso .dados .formacao {
    top: 61%;
    left: 2%;
  }

  .cartao.verso .presidente .presidente-dados, .cartao.verso .secretario .secretario-dados {
    position: absolute;
    bottom: 8%;
    font-size: 2.5vw;
    text-align: center;
    line-height: 1;
    width: 50%;
    color: rgb(55,55,55);
  }
  .cartao.verso .presidente .presidente-dados{
    left: 0;
  }
  .cartao.verso .secretario .secretario-dados {
    right: 0;
  }

  .cartao.verso .presidente img, .cartao.verso .secretario img {
    position: absolute;
    bottom: 17%;
    width: 50%;
    height: 13%;
    object-fit: contain;
  }
  .cartao.verso .presidente img {
    left: 0;
  }
  .cartao.verso .secretario img {
    right: 0;
  }

  @media (orientation: landscape) {
    .carp-view {
      transform: scale(.75) translateY(-15%);
    }
    .btn-group .btn {
      padding: .75rem 1.75rem;
      font-size: 1.125rem;
      line-height: 1.5;
    }
  }
</style>
