<template>
  <div class="signin-container">
    <div class="card" style="width: 23rem; max-width: 95vw">
      <div class="card-body">
        <header>
          <img class="logo" src="../../assets/icon/colibri.svg" alt="logo">
          <h5 class="titulo">i-Corretor</h5>
        </header>
        <form @submit.prevent="submit" ref="form">
          <!-- regional -->
          <div class="form-group">
            <label for="ipt-regional">Regional</label>
            <select id="ipt-regional" class="form-control" required v-model="iptRegional">
              <option :value="null" disabled="disabled">SELECIONE</option>
              <option v-for="regional in regionais" :key="regional.id" :value="regional.id">
                {{regional.id}}º - {{regional.ufExtenso.toUpperCase()}}
              </option>
            </select>
            <small v-if="compatibilidadeParcial" class="form-text"><i class="fas fa-exclamation-triangle pr-2 text-warning"/>Este regional tem compatibilidade parcial</small>
          </div>
          <!-- cpf -->
          <div class="form-group">
            <label for="ipt-cpf">CPF</label>
            <the-mask id="ipt-cpf" type="tel" autocomplete="username" class="form-control" mask="###.###.###-##"
                      required v-model="iptCpf"/>
          </div>
          <!-- senha -->
          <div class="form-group">
            <label for="ipt-senha">Senha</label>
            <input id="ipt-senha" type="password" minlength="6" maxlength="25" autocomplete="current-password"
                   class="form-control" required v-model="iptSenha">
            <router-link :to="{name: 'recover-password'}">Esqueci minha senha</router-link>
          </div>
          <!-- botao -->
          <button ref="btnsubmit" type="submit" class="btn btn-primary btn-block mt-4" :disabled="disableSubmit">
            ACESSAR
          </button>
          <!-- apagar o :disabled quando fornecer suporte a todos os regionais -->
        </form>
      </div>
    </div>
    <router-link :to="{name: 'signup'}" class="d-block text-center mt-3">Não é cadastrado no App? Clique aqui.</router-link>
  </div>
</template>

<script>
  import BrazilHelper from "../../helpers/BrazilHelper";
  import TokenHelper from "../../helpers/TokenHelper";
  import CreciHelper, {regionais} from "../../helpers/CreciHelper";

  export default {
    name: "Signin",
    data: () => ({
      regionais,
      iptRegional: null,
      iptCpf: '',
      iptSenha: '',
      disableSubmit: false
    }),
    methods: {
      async submit() {
        //Validações
        if (!BrazilHelper.validarCpf(this.iptCpf)) {
          this.$toastr.warning("CPF inválido");
          return;
        }
        if (!this.iptRegional) {
          this.$toastr.error("Regional inválido");
          return;
        }

        //Bloqueio da interface
        this.formDisabled();

        //Montagem da requisição
        let formData = new FormData();
        formData.append("cpf", this.iptCpf);
        formData.append("senha", this.iptSenha);
        formData.append("idregional", this.iptRegional);

        //Comunicação com o servidor do App
        let valido = true;
        let token = null;
        try {
          const r = await this.$http.appApi.post('/login.php', formData);

          //Valida se o Token foi recebido
          if (typeof r.data !== "string") {
            this.$toastr.error('Não foi possível validar o seu login, tente mais tarde');
            throw false;
          }

          //Valida se o token possui os dados essenciais para a aplicação (id, cpf, regional)
          token = new TokenHelper(r.data);
          if (!token.getDado('id') || !token.getDado('cpf') || token.getDado('regional') === undefined) {
            this.$toastr.error('Sua sessão não é válida', 'Autenticação imprópria');
            throw false;
          }
        } catch (e) {
          valido = false;
          this.formDisabled(false); //Reabilita a interface
        }

        //Comunicação com os servidores do Cofeci e Creci
        if (valido && token) {
          try {
            await this.$store.dispatch('signin', token.getToken());
            this.$router.push('/');
          } catch (e) {
            if (e && typeof e === "string") this.$toastr.error(e);
            else this.$toastr.error("Não foi possível entrar em contato com  o sistema COFECI/CRECI. Tente mais tarde.")
          } finally {
            this.formDisabled(false);
          }
        }
      },
      formDisabled(desativar = true) {
        //bloqueia/desbloqueia o formulario
        let form = this.$jquery(this.$refs.form);
        form.find('input, select').prop('disabled', desativar);
        this.disableSubmit = desativar;

        let btnsubmit = this.$jquery(this.$refs.btnsubmit);
        if (desativar) btnsubmit.html('<span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>');
        else btnsubmit.html('ACESSAR');
      }
    },
    created() {
      if (this.$route.query.cpf) this.iptCpf = this.$route.query.cpf;
      if (this.$route.query.regional) this.iptRegional = parseInt(this.$route.query.regional);
    },
    beforeRouteLeave(to, from, next) {
      if (to.path.startsWith('/auth/')) {
        if (this.iptRegional) to.query.regional = this.iptRegional;
        if (this.iptCpf) to.query.cpf = this.iptCpf;
      }
      next();
    },
    computed: {
      compatibilidadeParcial() {
        //o regional não usa sistema ConselhoNET, portanto, só depende da base de dados do COFECI
        return this.iptRegional !== null ? !CreciHelper.utilizaConselhoNet(this.iptRegional) && this.iptRegional !== 1 && this.iptRegional !== 4 : false;
      }
    }
  }
</script>

<style scoped>
  .signin-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
  }

  .card-body {
    box-shadow: inset 0 4px 0 0 #007bff;
    border-radius: .625rem;
  }

  .logo {
    display: block;
    margin: 0 auto;
    width: 4.5rem;
    max-height: 4.5rem;
    object-fit: contain;
  }

  .titulo {
    font-weight: 500;
    line-height: 1.5rem;
    text-align: center;
  }
</style>
