<template>
  <div class="list-group">
    <template v-if="$store.getters.cpf !== '04633822187'">
      <h5 class="list-group-item active text-center mb-0">MENU</h5>
      <!-- Cartão de Regularidade -->
      <router-link :to="{name: 'carp'}" class="list-group-item list-group-item-action d-flex">
        <img src="../assets/icon/carteira_digital.svg" alt="">
        Cartão de Regularidade
      </router-link>
      <!-- Certificado Empresarial -->
<!--      <router-link :to="{name: 'carp-pj-empresas'}" class="list-group-item list-group-item-action d-flex" v-if="$store.state.pj && $store.state.pj.length">-->
<!--        <img src="../assets/icon/certificado_pj.svg" alt="">-->
<!--        Certificado Empresarial-->
<!--&lt;!&ndash;        Certificado de Regularidade Empresarial&ndash;&gt;-->
<!--      </router-link>-->
      <!-- Recadastramento -->
      <button class="list-group-item list-group-item-action d-flex" @click="acessarRecadastramento(false)">
        <img src="../assets/icon/atualizacao_cadastral.svg" alt="">
        <span>Recadastramento</span>
      </button>
      <!-- Recadastramento PJ (Somente para responsaveis tecnicos e ainda indisponivel) -->
<!--      <button class="list-group-item list-group-item-action d-flex" @click="acessarRecadastramento(true)" v-if="$store.state.pj && $store.state.pj.length">-->
<!--        <img src="../assets/icon/atualizacao_cadastral_alt.svg" alt="">-->
<!--        <span class="d-flex align-items-center flex-wrap">-->
<!--          <span>Recadastramento PJ</span>-->
<!--          <span class="badge badge-pill badge-success ml-1" style="font-size: .5rem">NOVO</span>-->
<!--        </span>-->
<!--      </button>-->
      <!-- Palavra do presidente do COFECI -->
      <router-link :to="{name: 'palavra-cofeci'}" class="list-group-item list-group-item-action d-flex">
        <img src="../assets/icon/colibri_cofeci.svg" alt="">
        Palavra do Pres. COFECI
      </router-link>
      <!-- Palavra do presidente do CRECI -->
      <router-link :to="{name: 'palavra-creci'}" class="list-group-item list-group-item-action d-flex">
        <img src="../assets/icon/colibri_creci.svg" alt="">
        Palavra do Pres. CRECI
      </router-link>
    </template>
    <!-- Para demonstrações somente, o usuario específico é limitado: -->
    <template v-else>
      <h5 class="list-group-item active text-center">MENU</h5>
      <p class="list-group-item list-group-item-action">
        <img src="../assets/icon/carteira_digital.svg" alt="">
        Cartão de Identificação <span class="badge badge-secondary">EM BREVE</span>
      </p>
      <p class="list-group-item list-group-item-action">
        <img src="../assets/icon/atualizacao_cadastral.svg" alt="">
        Atualizar cadastro <span class="badge badge-secondary">EM BREVE</span>
      </p>
    </template>
    <!-- Spinner de carregamento para APIs -->
    <div v-if="loading"
         class="position-absolute vw-100 vh-100 d-flex justify-content-center align-items-center overflow-hidden"
         style="background-color: rgba(255, 255, 255, .6); top: 0; z-index: 9">
      <div class="spinner-border text-primary" role="status"></div>
    </div>
  </div>
</template>

<script>
  import CreciHelper from "../helpers/CreciHelper";
  export default {
    name: "Menu",
    data: () => ({
      loading: false
    }),
    computed: {
      compatibilidadeRecad() {
        return CreciHelper.utilizaConselhoNet(this.$store.getters.regional);
      }
    },
    methods: {
      async acessarRecadastramento(juridico = false) {
        try {
          this.loading = true;
          await this.$store.dispatch('updateAppConfig');
        } finally {
          this.loading = false;
        }

        try {
          // Verifica as configuracoes do recadastramento do regional do usuario
          if (this.$store.state.recadConfig) {
            if (this.$store.state.recadConfig.ativo) {
              if (this.$store.state.recadConfig.link) {
                // Leva o usuário a um site externo.
                if (this.$store.state.mobile) {
                  // === PLATAFORMA MOBILE ===
                  // eslint-disable-next-line no-undef
                  const ref = cordova.InAppBrowser.open(this.$store.state.recadConfig.link, "_blank");
                  ref.addEventListener("exit", () => this.$store.dispatch('updateUserData'));
                } else {
                  // === PLATAFORMA WEB ===
                  window.location.href = this.$store.state.recadConfig.link;
                }
              } else if (this.compatibilidadeRecad) {
                // Leva o usuario ao recad nativo.
                if (juridico) await this.$router.push({name: 'recadpj-menu'});
                else await this.$router.push({name: 'recad-menu'});
              } else {
                throw new Error("Seu regional não possui convênio com o recadastramento");
              }
            } else {
              throw new Error("Seu regional não habilitou o recadastramento");
            }
          } else {
            throw new Error("Seu regional não possui convênio com o recadastramento");
          }
        } catch (e) {
          this.$toastr.warning(e.message, "Indisponível");
        }
      }
    }
  }
</script>

<style scoped>
  .list-group-item img{
    height: 2rem;
    max-width: 2rem;
    object-fit: cover;
    margin-right: 1rem;
  }
  .list-group-item{
    border-radius: 0 !important;
    font-weight: 600;
  }
</style>
